.json.hljs {
  font-family: Roboto;
  color: #292937;
  font-size: 18px;
  font-weight: 500;
}

.hljs-attr {
  color: #292937;
}

.hljs-string {
  background-color: #00c391;
  background-image: linear-gradient(258.19deg, #00c391 -0.78%, #24c6dc 99.6%),
    #00c391;
  background: linear-gradient(258.19deg, #00c391 -0.78%, #24c6dc 99.6%), #00c391;
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.hljs-literal {
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-color: #dda045;
  background: linear-gradient(180deg, #dda045 0%, #ddce45 100%);
  background-image: linear-gradient(180deg, #dda045 0%, #ddce45 100%);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}

.hljs-number {
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-color: #e841e5;
  background: linear-gradient(180deg, #ec7f5c -19.33%, #e841e5 100%),
    linear-gradient(180deg, #ec7f5c -19.33%, #e841e5 100%);
  background-image: linear-gradient(180deg, #ec7f5c -19.33%, #e841e5 100%),
    linear-gradient(180deg, #ec7f5c -19.33%, #e841e5 100%);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
}
